import styled from "styled-components";

import Fade from 'react-reveal/Fade';

const ContentContainer = styled.div`
    height: 30vh;
    color: #616161;
    font-family: "MaruBuri";
    text-align: center;
    align-items: center;
    padding-top: 10%;
    padding-bottom: 30%;
`;

const TextDiv= styled.div`
    padding: 2%;
    font-weight: 200;
`;
const TextDivBold = styled.div`
    padding: 5%;
    font-weight: 400;
    
`;

export default function BodyContainer() {
    return (
        <div>
        <ContentContainer>
            <Fade bottom>
                <TextDivBold>.</TextDivBold>
            </Fade>
            <Fade bottom>
                <TextDiv>9  </TextDiv>
            </Fade>
            <Fade bottom>
                <TextDiv>.</TextDiv>
            </Fade>
            <Fade bottom>
                <TextDiv>  </TextDiv>
            </Fade>
            <Fade bottom>
                <TextDiv> .</TextDiv>
            </Fade>
            <Fade bottom>
                <TextDiv>.</TextDiv>
            </Fade>
        </ContentContainer>
    </div>);
}