import styled from "styled-components";
import mainImg from "../images/main.jpg";
import "../App.css";

const StyledContainer = styled.div`
    background-size: 100%;
    background-image: url(${mainImg});
    background-repeat: no-repeat;
`;

const NameDiv = styled.div`
    color: #595454;
    padding: 5%;
    font-family: "MaruBuri";
    font-weight: 100;
    display: flex;
`;
const Contents = styled.div`
    font-size: 100%;
    text-align: center;
    color: #e8e8e8;
    font-family: "MaruBuri";
    font-weight: 100;
    padding-top: 130%;
    padding-bottom: 30%;
`;

export default function TopContainer () {
    return(
        <StyledContainer>
            <NameDiv>
                <div class="first"></div>
                <div class="second"></div>
            </NameDiv>
            <Contents>
                <div>2022 9 18</div>
                <div>  3</div>
                <div> </div>
                <div> ()</div>
            </Contents>
        </StyledContainer>
    );
}